* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* General Styles */
body {
  font-family: "Arial", sans-serif;
  background: linear-gradient(135deg, #f0f4fd, #d9e6fc);
  margin: 0;
  padding: 0;
  padding-block: 3rem;
  display: flex;
  justify-content: center;
  align-items: start;
  height: 100vh;
  color: #333;
}

h1 {
  font-size: 2.5rem;
  text-align: center;
  color: #1e90ff;
  margin-bottom: 20px;
}

h2 {
  font-size: 2rem;
  color: #2e8b57;
  margin-bottom: 10px;
}

button {
  background-color: #1e90ff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #006bb3;
}

/* Container Styles */
.main {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  width: 100%;
  text-align: center;

  margin-bottom: 2rem;
}

.main div {
  margin-top: 1rem;
  width: 100%;
}

.add {
  color: #000;
  background: linear-gradient(to right, rgb(134, 218, 69), rgb(198, 229, 42));
}
.delete {
  background: linear-gradient(to right, rgb(211, 64, 64), rgb(225, 114, 73));
}

/* Input Styles */
input[type="email"],
input[type="password"] {
  width: 70%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 1rem;
  outline: none;
}

input[type="password"] {
  width: 70%;
}

input[type="email"]:focus,
input[type="password"]:focus {
  border-color: #1e90ff;
  box-shadow: 0 0 5px rgba(30, 144, 255, 0.5);
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  background-color: #f9f9f9;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  /* display: flex; */
  justify-content: space-between;
  align-items: center;
  border: 1px solid #ddd;
}

/* Checkbox Styles */
input[type="checkbox"] {
  transform: scale(1.5);
  margin: 0 10px;
}

span {
  display: flex;
  justify-content: center;
}

label {
  font-size: 1rem;
  margin-right: 10px;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Link Button */
a {
  text-decoration: none;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* Add/Delete Buttons */
button.add-btn {
  background-color: #28a745;
  margin-left: 10px;
}

button.add-btn:hover {
  background-color: #218838;
}

button.delete-btn {
  background-color: #ff6347;
  margin-left: 10px;
}

button.delete-btn:hover {
  background-color: #e53e3e;
}

/* List Email Styling */
.email-list-item {
  font-size: 1.2rem;
  color: #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.email-list-item label {
  font-size: 1rem;
  color: #333;
}

/* Responsive */
@media (min-width: 768px) {
  .margin {
    margin-left: 2rem;
  }
}
@media (max-width: 768px) {
  input[type="email"] {
    width: 100%;
  }

  div {
    width: 90%;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  button {
    width: 100%;
    margin-top: 10px;
  }

  li {
    flex-direction: column;
    align-items: flex-start;
  }

  li button {
    width: 100%;
    margin-top: 5px;
  }
}
